import React, { Fragment, useEffect, useState } from "react";
import logo from "../../styles/assets/images/Logo/favicon-removebg.png";
import megaMeunLogo from "../../styles/assets/images/Logo/offzname-logo-removebg-preview.png";
import { Link } from "gatsby";
import { Navbar, Container, Nav, Offcanvas, Tab } from "react-bootstrap";
import TalkToUs from "./talk-to-us";
import { yupResolver } from "@hookform/resolvers/yup";
import * as Yup from "yup";
import { Modal } from "react-bootstrap";
import Loading from "./loading";
import { useForm } from "react-hook-form";
import { Country } from "country-state-city";
import { collection, addDoc } from "firebase/firestore/lite";
import { utils } from "../../core/helper";

const validationSchema = Yup.object().shape({
  name: Yup.string().label("Name").required(),
  email: Yup.string().label("Email").required(),
  country: Yup.string().label("Country").required(),
  mobile: Yup.string().label().required("Required"),
  content: Yup.string().label("Content").required(),
});

const Header = (props) => {
  const { db } = props;
  console.log("TalkToUs::", db);

  const [toggleShow, setToggleShow] = useState(false);
  const [loading, setLoading] = useState(false);
  const [talkToUsShow, setTalkToUsShow] = useState(false);
  const [toggleMegaMenuShow, setToggleMegaMenuShow] = useState(false);

  const [urlParams, setUrlParams] = useState([]);
  console.log("urlParams::", urlParams);

  useEffect(() => {
    if (typeof window !== `undefined` || undefined) {
      const queryString = window.location.pathname;
      const splitQuery = queryString.split("/");
      const removeEmptyValues = splitQuery.filter((ele) => ele);
      if (queryString) {
        setUrlParams(removeEmptyValues);
      }
    }
  }, []);

  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    resolver: yupResolver(validationSchema),
  });

  const handleToggleShow = () => setToggleShow(!toggleShow);
  const handleToggleTalkToUs = () => {
    setTalkToUsShow(!talkToUsShow);
    reset();
  };
  const handleToggleMegaMenu = () => setToggleMegaMenuShow(!toggleMegaMenuShow);
  const countries = Country.getAllCountries();

  const handleOnSubmit = async (values) => {
    const { name, email, country, content, mobile } = values;
    const payload = {
      name,
      email,
      country,
      content,
      mobile,
      date: new Date(),
    };
    setLoading(true);
    try {
      const clients = collection(db, "clients");
      await addDoc(clients, payload);
      utils.showSuccssMsg("We will contact/talk to you soon");
      reset();
      setLoading(false);
      setTimeout(() => {
        handleToggleTalkToUs();
      }, 1000);
    } catch (e) {
      utils.showErrMsg("Error adding document");
      console.log("e::", e);
      setLoading(false);
    }
  };

  return (
    <Fragment>
      <Navbar expand="lg" className="navbar-expand-lg header">
        <Container fluid>
          <Link to="/" className="ms-5">
            <img className="logoheader" src={logo} alt="jusstry-logo" />
          </Link>
          <Navbar.Toggle
            aria-controls="basic-navbar-nav"
            onClick={handleToggleShow}
            className={`${toggleShow ? "x collapsed" : ""}`}
          >
            <div className="icon-bar"></div>
            <div className="icon-bar"></div>
            <div className="icon-bar"></div>
          </Navbar.Toggle>
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav
              className="navbar-nav nav-underline ms-auto d-flex justify-content-around align-items-center"
              as={"ul"}
            >
              <Nav.Item as={"li"}>
                <Link
                  className={`nav-link fw-bold text-uppercase  text-white fs-3 ${urlParams.map(
                    (ele) => (ele === "blog" ? "nav_link_active " : "")
                  )} `}
                  href="/blog"
                >
                  Blogs
                </Link>
              </Nav.Item>
              <Nav.Item onClick={handleToggleMegaMenu} as={"li"}>
                <a
                  className={`nav-link fw-bold text-uppercase fs-3  text-white ${urlParams.map(
                    (ele) => (ele !== "blog" ? "nav_link_active " : "")
                  )}`}
                >
                  Services
                </a>
              </Nav.Item>
              <Nav.Item className="text-uppercase">
                <button
                  onClick={handleToggleTalkToUs}
                  className="custom-btn btn--outline text-dark fw-bold text-uppercase"
                >
                  talk to us
                </button>
              </Nav.Item>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
      <Offcanvas
        show={toggleMegaMenuShow}
        onHide={handleToggleMegaMenu}
        placement="end"
        className="offcanvas-mega-menu"
      >
        <Offcanvas.Header closeButton>
          <img
            className="logoheader"
            id="offcanvasRightLabel"
            src={megaMeunLogo}
            alt="justtry-logo"
          />
        </Offcanvas.Header>
        <Offcanvas.Body>
          <Tab.Container defaultActiveKey="services">
            <div className="mega-menu">
              <div className="d-flex align-items-start">
                <Nav className="main-menu flex-column" variant="pills">
                  <Nav.Item>
                    <Nav.Link eventKey="products">Products</Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link eventKey="services">Services</Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link>
                      <Link to="/blog">Blogs</Link>
                    </Nav.Link>
                  </Nav.Item>
                  {/* <Nav.Item>
                    <Nav.Link eventKey="contact-us">Contact Us</Nav.Link>
                  </Nav.Item> */}
                </Nav>
                <Tab.Content>
                  <Tab.Pane eventKey="products">
                    <Nav className="main-menu flex-column" variant="pills">
                      <Nav.Item>
                        <Link
                          target="_blank"
                          className="nav-link"
                          to="https://partzrider.com/"
                        >
                          Partzrider
                        </Link>
                      </Nav.Item>
                      <Nav.Item>
                        <Link
                          target="_blank"
                          className="nav-link"
                          to="https://www.truefriendmatrimony.com/"
                        >
                          Trufriend Matrimony
                        </Link>
                      </Nav.Item>
                      <Nav.Item>
                        <Link
                          className="nav-link"
                          target="_blank"
                          to="https://dev.uttimo.com/"
                        >
                          Uttimo
                        </Link>
                      </Nav.Item>
                    </Nav>
                  </Tab.Pane>
                  <Tab.Pane eventKey="services">
                    <Nav className="main-menu flex-column" variant="pills">
                      <Nav.Item>
                        <Nav.Link>
                          <Link to="/website-development">
                            Website Development
                          </Link>
                        </Nav.Link>
                      </Nav.Item>
                      <Nav.Item>
                        <Nav.Link>
                          <Link to="/mern-development">
                            MERN / MEAN Stack Development
                          </Link>
                        </Nav.Link>
                      </Nav.Item>
                      <Nav.Item>
                        <Nav.Link>
                          <Link to="/smart-contract">
                            Smart Contract Development
                          </Link>
                        </Nav.Link>
                      </Nav.Item>
                      <Nav.Item>
                        <Nav.Link>
                          <Link to="/poc-open-source">
                            POC Open Source Development
                          </Link>
                        </Nav.Link>
                      </Nav.Item>
                      <Nav.Item>
                        <Nav.Link>
                          <Link to="/crypto-development">
                            Crypto Development
                          </Link>
                        </Nav.Link>
                      </Nav.Item>
                    </Nav>
                    <Tab.Content></Tab.Content>
                  </Tab.Pane>

                  <Tab.Pane eventKey="contact-us">
                    <Nav className="main-menu flex-column" variant="pills">
                      <Nav.Item>
                        <Nav.Link eventKey="sub-contact">
                          Sub Contact Us
                        </Nav.Link>
                      </Nav.Item>
                    </Nav>
                  </Tab.Pane>
                </Tab.Content>
              </div>
            </div>
          </Tab.Container>
        </Offcanvas.Body>
      </Offcanvas>
      <TalkToUs
        show={talkToUsShow}
        onHide={handleToggleTalkToUs}
        closeButton={true}
        size={"lg"}
      >
        <div class="screen__content">
          <form onSubmit={handleSubmit(handleOnSubmit)} class="login">
            <div class="login__field">
              <i class="login__icon fas fa-user"></i>
              <input
                type="text"
                class="login__input"
                {...register("name")}
                placeholder="Your Name"
              />
              <p className="text-danger mb-0">{errors.name?.message}</p>
            </div>
            <div class="login__field">
              <i class="login__icon fas fa-lock"></i>
              <input
                type="email"
                class="login__input"
                {...register("email")}
                placeholder="Email"
              />
              <p className="text-danger mb-0">{errors.email?.message}</p>
            </div>
            <div class="login__field">
              <i class="login__icon fas fa-lock"></i>
              <input
                type="text"
                class="login__input"
                placeholder="Skype / Mobile"
                {...register("mobile")}
              />
              <p className="text-danger mb-0">{errors.mobile?.message}</p>
            </div>
            <div class="login__field">
              <i class="login__icon fas fa-lock"></i>
              <select class="login__input" {...register("country")}>
                <option value="">Select</option>
                {countries.map((ele, ind) => {
                  return (
                    <option key={ind} value={ele.isoCode}>
                      {ele.name}
                    </option>
                  );
                })}
              </select>
              <p className="text-danger mb-0">{errors.country?.message}</p>
            </div>
            <div class="login__field">
              <i class="login__icon fas fa-lock"></i>
              <textarea
                id="content"
                placeholder="Type content here"
                rows={5}
                className="login__input"
                {...register("content")}
                maxLength={200}
              ></textarea>
              <p className="text-danger mb-0">{errors.content?.message}</p>
            </div>
            <button disabled={loading} class="button login__submit">
              <span class="button__text"> Submit {loading && <Loading />}</span>
              <i class="button__icon fas fa-chevron-right"></i>
            </button>
          </form>
        </div>
        <div class="screen__background">
          <span class="screen__background__shape screen__background__shape4"></span>
          <span class="screen__background__shape screen__background__shape3"></span>
          <span class="screen__background__shape screen__background__shape2"></span>
          <span class="screen__background__shape screen__background__shape1"></span>
        </div>
      </TalkToUs>
    </Fragment>
  );
};

export default Header;
