import React, { useState } from "react";
import logo from "../../styles/assets/images/Logo/Justtry logo-whitebg.png";
import sendIcon from "../../styles/assets/images/Logo/send.png";
// import whatsapp from "../../styles/assets/images/Socialicons/whatsapp.png";
// import email from "../../styles/assets/images/Socialicons/email.png";
// import linkedin from "../../styles/assets/images/Socialicons/linkedin.png";
import addToMailchimp from "gatsby-plugin-mailchimp";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as Yup from "yup";
import { utils } from "../../core/helper";
import { Spinner } from "react-bootstrap";

const validationSchema = Yup.object().shape({
  email: Yup.string().label("Email").required(),
});

const Footer = () => {
  const [loading, setLoading] = useState(false);
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    resolver: yupResolver(validationSchema),
  });

  const handleSubscribe = async (values) => {
    console.log("values::", values);
    setLoading(true);
    const { email } = values;
    const resp = await addToMailchimp(email);
    console.log("resp::", resp);
    if (resp.result === "success") {
      utils.showSuccssMsg(resp?.msg);
      setLoading(false);
      reset();
    } else {
      utils.showErrMsg(resp?.msg);
      setLoading(false);
    }
  };

  return (
    <footer className="footer mt-5 rounded">
      <div className="row p-3 ">
        <div className="col-md-3 p-5 d-flex align-items-center">
          <img className="w-100" alt="Justtry logo" src={logo} />
        </div>
        <div className="col-md-4 p-5 ">
          <p className="h2 pb-5">Contact us</p>
          <address>
            <p>
              179/3, Kamarajar salai,
              <br></br> (Near Tamilnadu Chamber of Commerce),<br></br>
              Madurai  625009.
            </p>
            <p>
              <a className="footer-link" href="tel:+91 9500139200">
                +91 - 9500139200
              </a>
            </p>
            <p>
              {" "}
              <a
                className="footer-link"
                href="mailto:justtrytechnologies@gmail.com"
              >
                justtrytechnologies@gmail.com
              </a>
            </p>
          </address>
        </div>
        <div className="col-md-2 p-5  d-flex flex-column align-items-start justify-content-start">
          <p className="h2 pb-5">Company</p>
          <p>
            <a className="footer-link" href="#about">
              About Us
            </a>
          </p>
          {/* <p>
            <a className="footer-link" href="#">
              For Business
            </a>
          </p>
          <p>
            <a className="footer-link" href="#clients">
              Client Partners
            </a>
          </p> */}
        </div>
        <div className="col-md-3 py-5">
          <p className="h2 pb-5">Subscribe to our news letter</p>
          <p className="mb-3">Get updates from Justtry Technologies</p>
          <div className="footer-form pb-4 d-flex align-items-center justify-content-start">
            <form onSubmit={handleSubmit(handleSubscribe)}>
              <div className="d-flex">
                <input
                  type="email"
                  placeholder="Subscribe for more updates"
                  {...register("email")}
                />
                <button type="submit">
                  {loading && <Spinner animation="border" variant="light" />}
                  {!loading && <img className="send-icon" src={sendIcon} />}
                </button>
              </div>
              <span className="text-danger fs-4">{errors?.email?.message}</span>
            </form>
            {/* <a className="footer-link me-5" href="#">
              <img className="social-icon" src={linkedin} alt="linkedin" />
            </a>
            <a
              className="footer-link me-5"
              href="mailto:justtrytechnologies@gmail.com"
            >
              <img className="social-icon" src={email} alt="Email" />
            </a>
            <a className="footer-link me-5" href="https://wa.me/9500139200">
              <img className="social-icon" src={whatsapp} alt="whatsapp" />
            </a> */}
          </div>
          <p>Copyright &copy; 2023 by Justtry Technologies Private Limited.</p>
        </div>
      </div>
      {/* <div className="row">
        <p>
          Copyright &copy; 2023 by Justtry Technologies Private Limited. All
          rights reserved.
        </p>
      </div> */}
      {/* <div className="icons">
        <div className="social">
          <a href="#">
            <i className="social-icon fab fa-facebook-f"></i>
          </a>
        </div>
        <div className="social">
          <a href="#">
            <i className="social-icon fab fa-twitter"></i>
          </a>
        </div>
        <div className="social">
          <a href="#">
            <i className="social-icon fab fa-google-plus-g"></i>
          </a>
        </div>
        <div className="social">
          <a href="#">
            <i className="social-icon fab fa-instagram"></i>
          </a>
        </div>
        <div className="social">
          <a href="#">
            <i className="social-icon fab fa-linkedin-in"></i>
          </a>
        </div>
      </div> */}
    </footer>
  );
};

export default Footer;
